    margin-bottom:200px;
    //will-change:scroll-position;//This literally ruins the scrolling flow
    a {
      figure {
        position:relative;
        height:320px;
        .project-thumbnail {
          position:absolute;
          top:0;
          right:0;
          bottom:0;
          left:0;
          overflow:hidden;
        }
