            position:absolute;
            width:calc(100% - 1px);
            height:20px;
            bottom:-30px;
            left:1px;
            font-size:13px;
            overflow:hidden;
            pointer-events:none;
            > .project-data, > .project-platforms {
              @include transition(transform cubic-bezier(0.455,0.03,0.515,0.955) 1s, filter cubic-bezier(0.455,0.03,0.515,0.955) 1s);
            }
            > .project-data {
              @include filter(opacity(1));
