// Footer
footer {
  margin:0;
  > a.container {
    min-height:100vh;
    > div {//From "row", To All Elements Inside <a>
      pointer-events:none;
      h1, h2, h3, h4, h5, h6 {
      }
      p {
        font-size:23px;
        line-height:1.2;
      }
    }
  }
  aside.footer-info-elements {
    .footer-hello, .footer-office, .footer-business-social, .footer-copyright, .footer-email-or-call, .footer-created-by, .footer-personal-social, nav.footer-nav, .footer-credits {
      position:absolute;
      font-size:12px;
      opacity:(0.9);//@include filter(opacity(0.9))
      a {
        position:relative;
        &::before {
          content:'';
          position:absolute;
          width:0;
          top:50%;
          right:0;
          //left:0;
          border-bottom:1px solid theme-color('rosybrown');
          pointer-events:none;
          @include transition(width 0.3s ease-in-out);
        }
        &:hover, &:active, &:focus {
          &::before {
            width:100%;
            right:auto;
            left:0;
          }
        }
      }
    }
    .footer-business-social, .footer-personal-social {
      overflow:hidden;
      a {
        &::before {
          display:none;
        }
        > span {
          position:relative;
          font-size:15.5px;
          //pointer-events:none;
          > svg {
            position:relative;
            width:17px;
            font-size:1.2rem;
            fill:theme-color('rosybrown')!important;
            z-index:21;
            @include transition(all ease-out 0.5s);
          }
          &::before {
            content:'';
            position:absolute;
            width:27px;
            height:27px;
            top:-2px;//-2.8px
            left:-5px;
            background-color:theme-color('rosybrown');
            z-index:20;
            @include border-radius(50%);
            @include filter(opacity(0));
            @include transform(translateY(30px));
            //@include transform-origin(center bottom);
            @include transition(filter ease-out 0.5s, transform ease-out 0.5s);
          }
          &.icon-linkedin {
            svg {
              width:14px;
              margin-top:-1px;
              margin-left:2px;
            }
          }
          &.icon-behance {
            svg {
              width:18px;
            }
          }
          &.icon-angel {
            svg {
              width:14px;
              margin-left:1px;
            }
          }
          // &.icon-github {
          //   svg {
          //   }
          // }
          // &.icon-dribbble {
          //   svg {
          //   }
          // }
          // &.icon-pinterest {
          //   svg {
          //   }
          // }
          // }
          // &.icon-instagram {
          //   svg {
          //   }
          // }
          &.icon-facebook {
            svg {
              width:16px;
            }
          }
        }
        &:hover, &:active, &:focus {
          span {
            svg {
              fill:theme-color('chocolate')!important;
            }
            &::before {
              @include filter(opacity(1));
              @include transform(translateY(0));
            }
          }
        }
      }
    }
    .footer-hello {
      bottom:370px;
      left:37px;
      line-height:1.5;
    }
    .footer-office {
      bottom:220px;
      left:37px;
    }
    .footer-business-social {
      padding:0 7px 5px;
      bottom:115px;//120px
      left:30px;//37px
    }
    .footer-copyright {
      bottom:30px;
      left:37px;
    }
    .footer-email-or-call {
      bottom:30px;
      left:50%;
      text-align:center;
      @include transform(translateX(-50%));
    }
    .footer-created-by {
      right:37px;
      bottom:30px;
      text-align:right;
    }
    .footer-personal-social {
      padding:0 7px 5px;
      right:30px;//37px
      bottom:115px;//120px
      text-align:right;
    }
    nav.footer-nav.navbar {
      padding:0;
      top:auto;
      right:37px;
      bottom:228px;
      line-height:1.5!important;
      text-align:right;
      ul.navbar-nav {
        li.nav-item {
          a.nav-link {
            &::before {
              display:none;
            }
          }
        }
      }
    }
    .footer-credits {
      right:37px;
      bottom:370px;
      line-height:1.5;
      text-align:right;
    }
  }
